import TimelineOppositeContent, {timelineOppositeContentClasses} from "@mui/lab/TimelineOppositeContent";
import {timelineItemClasses} from "@mui/lab/TimelineItem";
import {Timeline, TimelineConnector, TimelineContent, TimelineDot, TimelineItem, TimelineSeparator} from "@mui/lab";
import {CalendarMonth, Place, WorkspacePremium} from "@mui/icons-material";
import {List, ListItem, ListItemIcon, ListItemText, Typography} from "@mui/material";

const eduTimeline = [{
  name: 'Diploma di istruzione superiore',
  start: '2014',
  end: '2019',
  place: 'Istituto Tecnico J. F. Kennedy, Monselice (PD)',
  content: 'Diploma di specializzazione di 1° livello in informatica e tecnologia DS1.'
}, {
  name: 'Laurea triennale in Informatica',
  start: 'Ottobre 2019',
  end: 'Dicembre 2022',
  place: 'Dipartimento di Matematica Tullio-Levi Civita, Università degli Studi di Padova, Padova (PD)',
  content: 'Dopo aver conseguito il titolo di tecnico superiore, ho scelto di proseguire gli studi iscrivendomi ' +
      'al corso di laurea in Informatica presso l\'Università di Padova. Durante questo periodo ho avuto modo di ' +
      'approfondire alcuni concetti e di apprenderne di nuovi, inoltre ho svolto numerosi progetti, sia ' +
      'singolarmente che in gruppo. Questo percorso si è concluso con lo svolgimento di un progetto commissionato ' +
      'da un\'importante azienda che opera a livello nazionale e non solo, e infine con un tirocinio svolto presso ' +
      'una startup della mia zona.'
}];

function Education() {
  return (
      <Timeline sx={{
        '&': {
          height: '100%',
          margin: 0,
          justifyContent: 'space-between'
        }, [`& .${timelineOppositeContentClasses.root}`]: {
          flex: 0,
          padding: 0
        }, [`& .${timelineItemClasses.root}`]: {
          flexGrow: 1
        }
      }}>
        {eduTimeline.map((it, key) => (
            <TimelineItem key={key}>
              <TimelineOppositeContent></TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot color="primary">
                  <WorkspacePremium fontSize="small" />
                </TimelineDot>
                {key !== eduTimeline.length - 1 && <TimelineConnector />}
              </TimelineSeparator>
              <TimelineContent>
                <Typography variant="h3">
                  {it.name}
                </Typography>
                <List>
                  {it.place && (
                      <ListItem>
                        <ListItemIcon><Place fontSize="small" /></ListItemIcon>
                        <ListItemText>{it.place}</ListItemText>
                      </ListItem>
                  )}
                  {it.start && (
                      <ListItem>
                        <ListItemIcon><CalendarMonth fontSize="small" /></ListItemIcon>
                        <ListItemText>{it.start}{it.end && ' - ' + it.end}</ListItemText>
                      </ListItem>
                  )}
                </List>
                {it.content && (
                    <Typography>{it.content}</Typography>
                )}
              </TimelineContent>
            </TimelineItem>
        ))}
      </Timeline>
  );
}

export default Education;
